___CSS_LOADER_EXPORT___.push([module.id, `* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    scroll-behavior: smooth;
    list-style: none;
    text-decoration: none;
}

:root {
    --main-color: #E1A90C;
    --bg-color: linear-gradient(to right, #F4C237 , #E1A90C);
    --bg-btn-active: linear-gradient(to right, #056AAD , #045285);
    --font-main: #1A1A1A;
    --font-desc: #363636;
    --color-white :#E5E5E5;
    --font-head : 32px;
  }

body,
html {
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100%;
}

body {
    font-family: 'Nunito', sans-serif;
    font-size: 12px;
    color: var(--font-main);
}

.skip-to-content {
    position: absolute;
    z-index: 2;
}

.skip-to-content a {
    color: var(--color-white);
}

header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: fixed;
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    background: var(--color-white);
    padding: 8px 7%;
    transition: all .50s ease;
    z-index: 1;
}

.logo {
    display: flex;
    align-items: center;
    height: 50px;
    padding-inline: 1rem;
  }
  .logo img {
    height: 18px;
    width: auto;
  }
  
  .navbar {
    display: flex;
    flex-direction: row-reverse;
    gap: 1rem;
    width: 100%;
  }
  
  .nav-list {
    display: flex;
    gap: 1rem;
  }
  
  .nav-item li {
    padding-left: 0;
    padding-right: 0;
  }
  
.nav-item a {
    Display: flex;
    align-items: center;
    font-size: .9rem;
    font-weight: 400;
    padding-left: 1rem;
    padding-right: 1rem;
    color: var(--font-main);
    transition: all .40s ease;
    min-height: 50px;
}
  
.nav-item a:hover {
    color: var(--main-color);
    border-bottom: 1px solid var(--main-color);
}
#drawer-btn {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 50px;
}
#drawer{
    display: flex;
    align-items: center;
}
  
.menu-btn {
    display: flex;
    align-items: center;
    font-size: var(--font-head);
    font-weight: 600;
    min-height: 50px;
    padding-inline: 10px;
}

.hero {
    display: flex;
    align-items: center;
    min-height: 380px;
    text-align: center;
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}

.hero-container {
    margin: 0 auto;
    max-width: 800px;
  }
  
.hero-title {
    color: #fff;
    font-weight: 600;
    font-size: 36px;
}
  
.hero-tagline {
    color: #fff;
    margin-top: 16px;
    font-size: 18px;
    font-weight: 300;
}

main {
    width: 100%;
    margin: 0 auto;
}

.content {
    padding: 32px;
}

.explore {
    width: 100%;
    margin: 60px auto;
    text-align: center;
}

.explore-label {
    font-size: var(--font-head);
    font-weight: lighter;
}

.explore-label::after {
    content: '';
    margin-top: 16px;
    display: block;
    border-bottom: 1px solid var(--main-color);
  }

.restaurants {
    margin: 32px auto auto;
    display: grid;
    grid-row-gap: 16px;
}

.rest-item {
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
    width: 100%;
    border-radius: 5px;
    overflow: hidden;
}
.wraper{
    position: relative;
}

.rest-city {
    position: absolute;
    top: 3%;
    border: 1px solid var(--main-color);
    background-color: var(--color-white);
}

.rest-city p {
    padding: .5rem;
    color: var(--font-main);
}

.rest-item__thumb {
    width: 100%;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
}

.rest-item__content {
    text-align: left;
    padding: 0.5rem 1rem;
}

.rest-item__content h2 {
    font-size: 14px;
}


.rest-item__title {
    display: flex;
    align-items: center;
    height: 50px;
    font-size: 18px;
    transition: 0.3s opacity;
    text-transform: capitalize;
}

.rest-item__title a {
    text-decoration: none;
    color: #E1A90C;
    padding-block: 15px;
}

.rest-item__title:hover{
    opacity: 0.7;
}

.rest-item__description {
    font-size: 12px;
    line-height: 1.5em;  
}

.footer {
    background-color: var(--main-color);
    padding: 2em;
    width: 100%;
    text-align: center;
}

.footer a{
    color: var(--font-main);
    font-size: 16px;
    font-weight: 600;
    padding: 15px 10px;
}

@media screen and (max-width: 537px) {
    .navbar {
        z-index: 1;
        background-color: #fff;
        width: 300px;
        position: absolute;
        -webkit-transform: translate(300px, 0);
        transform: translate(300px, 0);
        transition: transform 0.3s ease;
        top: 100%;
        right: 0;
    }

    .active {
        -webkit-transform: translate(0, 0);
        transform: translate(0, 0);
    }

    .nav-list {
        display: flex;
        flex-flow: column nowrap;
        justify-content: center;
        border-bottom: 1px solid #e0e0e0;
        width: 100%;
        padding-block: 0.5rem;
    }    
}
@media screen and (min-width: 538px) {
    #drawer-btn {
      display: none;
    }
}

@media screen and (min-width: 650px) {
    .rest-item__content {
        padding: 16px 32px 32px 32px;
    }
    .rest-item__title {
        font-size: 18px;
    }
    
    .rest-item__description {
        font-size: 14px;
    }
}

@media screen and (min-width: 700px) {
    .restaurants {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      grid-column-gap: 10px;
      grid-row-gap: 16px;
    }

    .rest-item__thumb {
        height: 250px;
    }
  }

@media screen and (min-width: 800px) {
    .hero {
        min-width: 480px;
    }
}

@media screen and (min-width: 900px) {
    .restaurants {
      display: grid;
      grid-template-columns: repeat(3, 1fr);
    }
  }

@media screen and (min-width: 1200px) {
    main {
      max-width: 1200px;
    }
    .nav-item a {
      font-size: 1.3rem;
    }
  }`, "",{"version":3,"sources":["webpack://./src/styles/main.css"],"names":[],"mappings":"AAEA;IACI,UAAU;IACV,SAAS;IACT,sBAAsB;IACtB,uBAAuB;IACvB,gBAAgB;IAChB,qBAAqB;AACzB;;AAEA;IACI,qBAAqB;IACrB,wDAAwD;IACxD,6DAA6D;IAC7D,oBAAoB;IACpB,oBAAoB;IACpB,sBAAsB;IACtB,kBAAkB;EACpB;;AAEF;;IAEI,SAAS;IACT,UAAU;IACV,WAAW;IACX,YAAY;AAChB;;AAEA;IACI,iCAAiC;IACjC,eAAe;IACf,uBAAuB;AAC3B;;AAEA;IACI,kBAAkB;IAClB,UAAU;AACd;;AAEA;IACI,yBAAyB;AAC7B;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,8BAA8B;IAC9B,eAAe;IACf,WAAW;IACX,iBAAiB;IACjB,kBAAkB;IAClB,8BAA8B;IAC9B,eAAe;IACf,yBAAyB;IACzB,UAAU;AACd;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,YAAY;IACZ,oBAAoB;EACtB;EACA;IACE,YAAY;IACZ,WAAW;EACb;;EAEA;IACE,aAAa;IACb,2BAA2B;IAC3B,SAAS;IACT,WAAW;EACb;;EAEA;IACE,aAAa;IACb,SAAS;EACX;;EAEA;IACE,eAAe;IACf,gBAAgB;EAClB;;AAEF;IACI,aAAa;IACb,mBAAmB;IACnB,gBAAgB;IAChB,gBAAgB;IAChB,kBAAkB;IAClB,mBAAmB;IACnB,uBAAuB;IACvB,yBAAyB;IACzB,gBAAgB;AACpB;;AAEA;IACI,wBAAwB;IACxB,0CAA0C;AAC9C;AACA;IACI,aAAa;IACb,uBAAuB;IACvB,mBAAmB;IACnB,WAAW;AACf;AACA;IACI,aAAa;IACb,mBAAmB;AACvB;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,2BAA2B;IAC3B,gBAAgB;IAChB,gBAAgB;IAChB,oBAAoB;AACxB;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,iBAAiB;IACjB,kBAAkB;IAClB,yDAAgE;IAChE,2BAA2B;IAC3B,4BAA4B;IAC5B,sBAAsB;AAC1B;;AAEA;IACI,cAAc;IACd,gBAAgB;EAClB;;AAEF;IACI,WAAW;IACX,gBAAgB;IAChB,eAAe;AACnB;;AAEA;IACI,WAAW;IACX,gBAAgB;IAChB,eAAe;IACf,gBAAgB;AACpB;;AAEA;IACI,WAAW;IACX,cAAc;AAClB;;AAEA;IACI,aAAa;AACjB;;AAEA;IACI,WAAW;IACX,iBAAiB;IACjB,kBAAkB;AACtB;;AAEA;IACI,2BAA2B;IAC3B,oBAAoB;AACxB;;AAEA;IACI,WAAW;IACX,gBAAgB;IAChB,cAAc;IACd,0CAA0C;EAC5C;;AAEF;IACI,sBAAsB;IACtB,aAAa;IACb,kBAAkB;AACtB;;AAEA;IACI,0CAA0C;IAC1C,WAAW;IACX,kBAAkB;IAClB,gBAAgB;AACpB;AACA;IACI,kBAAkB;AACtB;;AAEA;IACI,kBAAkB;IAClB,OAAO;IACP,mCAAmC;IACnC,oCAAoC;AACxC;;AAEA;IACI,cAAc;IACd,uBAAuB;AAC3B;;AAEA;IACI,WAAW;IACX,sBAAsB;IACtB,4BAA4B;IAC5B,kCAAkC;AACtC;;AAEA;IACI,gBAAgB;IAChB,oBAAoB;AACxB;;AAEA;IACI,eAAe;AACnB;;;AAGA;IACI,aAAa;IACb,mBAAmB;IACnB,YAAY;IACZ,eAAe;IACf,wBAAwB;IACxB,0BAA0B;AAC9B;;AAEA;IACI,qBAAqB;